import React, {useState} from "react";
import Navigation from "./components/Navigation";
import Body from "./components/Body";
import Header from "./components/Header";

function App() {
  const [display, setDisplay] = useState("");

  const [navigation, setNavigation] = useState([
    { name: 'Dashboard', href: '#Dashboard', current: true },
    { name: 'Calendar', href: '#Calendar', current: false },
    { name: 'Patients', href: '#Patients', current: false },
    { name: 'Reports', href: '#Reports', current: false },
    { name: 'Inventory', href: '#Inventory', current: false },
  ])

  function HandleNavClick(event) {
    var display = event.target.innerText;

    var nav_state = [...navigation];
    var nav_elem;

    for(var i = 0; i < nav_state.length; i++) {
      if(nav_state[i].name === display){
        nav_elem = { ...nav_state[i] };
        nav_elem.current = true;
      } else {
        nav_elem = { ...nav_state[i] };
        nav_elem.current = false;
      }
      
      nav_state[i] = nav_elem;
    }

    setDisplay(display);
    console.log(nav_state);
    setNavigation(nav_state);
  }

  return (
    <div class="page">
      <Navigation HandleNavClick={HandleNavClick} navigation={navigation}/>
      <Header name={display} />
      <div class="body">
        <Body display={display}/>
      </div>
    </div>
  );
}

export default App;
