import React from "react";
import Home from "./body/Home";
import Calendar from "./body/Calendar";
import Patients from "./body/Patients";
import Reports from "./body/Reports";
import Inventory from "./body/Inventory";

function Body(props) {
    if(props.display === "VetSys" || props.display === "Dashboard")
        return <Home />;
    else if(props.display === "Calendar")
        return <Calendar />;
    else if(props.display === "Patients")
        return <Patients />;
    else if(props.display === "Reports")
        return <Reports />;
    else if(props.display === "Inventory")
        return <Inventory />;
    else
        return <Home />;
}

export default Body;